





























































































































































































    declare global {
        interface Window{ PrismicJS: any, PrismicDOM: any }
    }	

    import { Component, Vue, Watch } from "vue-property-decorator";
	import { Route } from 'vue-router';
	import _Prismic from '@/prismic';
	
    Component.registerHooks(['beforeRouteEnter']);

	@Component
	export default class Events extends Vue { 
	    prismicDOM = window.PrismicDOM;
		viewEvent: boolean = false;
		current: any = {};
		events: any = {upcoming_events:[], special_announcement: [], services:[]};
		overlay = ' linear-gradient(rgba(7, 61, 95, 0.9), rgba(7, 61, 95, 0.9))';
			
		beforeRouteEnter(to: Route, from: Route, next: Function) {
			next((vm) => { vm._getPage(); });
		}

	    _viewEvent(p:any ) {
			this.current = p;
			this.viewEvent = true;
		}

		async _getPage() {
			var api = await _Prismic();
            this.events = (await api.getSingle('events')).data; 
		}

		_scrollTo(id: string) {
			var el = document.getElementById(id);
			el.scrollIntoView({behavior: 'smooth'});
		}

		backgroundImage(url) {
            return this.overlay + ',url("'+ url +'")';
        }
}
